@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  padding: 20px;
  font-family: 'Poppins', sans-serif;
  background-color: #F0F2F5;
}

@media screen and (min-width: 1200px) {
  .fixed-border {
    border-right: 1px solid rgb(230, 230, 230);
  }
}
.badge-self {
  width: 80px;
  height: auto;
}

.wins-color {
  color: #66D266;
}

.losses-color {
  color: #FF8787;
}

.draws-color {
  color: #AEAEAE;
}

.total-color {
  color: #E2E25C;
}

.description-self {
  font-size: 14px;
  max-width: 500px;
}

.war-stats-wrapper {
  background-color: white;
  border-radius: 8px;
  font-size: 15px;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.1);
}

.fontSemiBold {
  font-weight: 600;
}

.fontMedium {
  font-weight: 500;
}

.textSmall {
  font-size: 14px;
}
.heading {
  font-weight: 300;
  font-size: 28px;
  margin-bottom: 5px;
}

.jumbotronSelf {
  margin: 0px 0px 28px 0px;
  background-color: #FFFFFF;
  height: 300px;
  border-radius: 8px;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.1);
  padding: 0px;
  z-index: -1;
}
.bgPreparation {
  background-color: #824EFF;
}

.bgVictory {
  background-color: #6CD36C;
}

.fontColorVictory {
  color: #6CD36C;
}

.modalSelf {
  background-color: transparent;
  border: none;
}

.heading {
  font-weight: 300;
  font-size: 28px;
  margin-bottom: 5px;
}

.fontSemiBold {
  font-weight: 600;
}

.fontMedium {
  font-weight: 500;
}

.textSmall {
  font-size: 14px;
}

.scoreSelf {
  width: 65px;
  height: 60px;
  text-align: center;
  line-height: 60px;
  border-radius: 8px;
  background-color: #ECECEC;
  font-weight: 700;
  font-size: 18px;
}

.imageSelf {
  width: 40px;
  height: auto;
}

.pngSelf {
  width: 25px;
  height: auto;
}

.hero-image {
  width: 170px;
  right: 0px;
  top: -25px;
}

.pwButton {
  margin-top: 90px;
}
